import { $api } from "@/services/constant";
import { HOST_API_V1 } from "@/services/env-init";
import { AxiosRequestConfig } from "@/services/types";

const BASE_URL = HOST_API_V1 + "admin/";
const BASE_USER_URL = HOST_API_V1 + "admin/user/";
const BASE_TRANSAKSI_URL = HOST_API_V1 + "admin/transaksi/";

export const getUser = async (params: AxiosRequestConfig) => {
  const res = await $api().get(BASE_USER_URL, params);
  return res.data;
};

export const getUserById = async (userId: number) => {
  const res = await $api().get(BASE_USER_URL + userId);
  return res.data;
};

export const forgotPassword = async (payload: any) => {
  const res = await $api().put(BASE_URL + "forgot-password", payload);
  return res.data;
};

export const getTransaksiById = async (invoiceNumber: string) => {
  const res = await $api().get(BASE_TRANSAKSI_URL + `income/` + invoiceNumber);
  return res.data;
};

export const updateIncome = async (
  invoiceNumber: string,
  payload: any
) => {
  const res = await $api().put(
    BASE_TRANSAKSI_URL + `income/` + invoiceNumber,
    payload
  );
  return res.data;
};
